import React, { Component } from 'react';

import {
    Row,
    Col,
  } from "reactstrap";
import { faq } from "../../helpers/language";
import "./faq.scss";
import { Collapse, Button, CardBody, Card } from 'reactstrap';
class Faq extends Component {
    state = {

        qaArray: faq.questionList.map(item => ({ ...item, isOpen: false }))

    }
    toggle = (i) => {
        let { qaArray } = this.state;
        qaArray[i].isOpen = !qaArray[i].isOpen;
        this.setState({ qaArray })
    }
    render() {
        const { qaArray } = this.state;
        return (<div className="bg-white privacy-container mt-4" id="faq">
                <div className="fop-title mt-3">{faq.header}</div>
               
                <div className="faq-container mt-3 ">
                {qaArray.map((item, i, arr) => <div key={i}>
                    <Row className="m-5">
                        <Col md="6" xl="6"><div className="font-weight-bolder FAQ-question "><span>Q.</span>{item[0].q}</div><div className="FAQ-answer pb-2 mb-5">{item[0].children}
                            {
                            item[0].block ? item[0].block.map(ele => <div key={Math.random} className="block-item"><div className="block-head">{ele.header}</div><div className="block-data">{ele.value}</div></div>) : ""}</div></Col>
                        <Col md="6" xl="6"><div className="font-weight-bolder FAQ-question"><span>Q.</span>{item[1].q}</div><div className="FAQ-answer pb-2">{item[1].children}{
                            item[1].block ? item[1].block.map(ele => <div key={Math.random} className="block-item"><div className="block-head">{ele.header}</div><div className="block-data">{ele.value}</div></div>) : ""}</div></Col>
                    </Row>
                    </div>)}
                   
                        {/* <div className="ques-container" onClick={() => this.toggle(i)}><span className="faq-txt faq-ques-txt">Q.</span> {item.q}</div>
                        <Collapse isOpen={item.isOpen}>
                            <div className="ans-container"><span className="faq-txt faq-ques-txt">A.</span> {item.children.map(val => <React.Fragment><div> {val}</div></React.Fragment>)}{item.block ? item.block.map(ele => <div key={Math.random} className="block-item"><div className="block-head">{ele.header}</div><div className="block-data">{ele.value}</div></div>) : ""}</div>
                        </Collapse>
                    </div>) */}
                </div>
        </div>);
    }
}

export default Faq;